  -ms-flex-pack: justify;
  justify-content: space-between; }

.promo__tab {
  width: 20%;
  text-align: center;
  background: #252525;
  min-height: 155px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
